.home {
  padding: 0rem;
  padding-top: 2rem;
  margin-left: 1rem;
  margin-right: 2rem;
  padding-bottom: 3rem;
}

.home .block-container {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
}

.home .block {
  flex: 100%;
  display: flex;
  flex-direction: row;
  position: relative;
  gap: 1rem;
}

.home .sub-block {
  flex: 50%;
  min-height: 350px;
  /* border: 1px solid black; */
  /* justify-content: center; */
  align-items: center;
}

.home #profile {
  min-height: 350px;
  background-color: #4e596f;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  min-width: 50%;
  display: flex;
  flex-direction: row;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.home #profile h3 {
  color: #ccc;
  font-size: xx-large;
  /* align-self: flex-start; */
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.25rem;
  padding-bottom: 0.25rem;
  background-color: #4e596f;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-top-right-radius: 5px;
  border-bottom-right-radius: 5px;
  /* position: absolute; */
}

.home #profile img {
  margin: 0.7rem 2rem 0.5rem 0.7rem;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  /* box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5); */
}

.home #profile-more {
  background-color: #4e596f;
  min-height: 350px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  min-width: 50%;
  text-align: center;
  vertical-align: middle;
  gap: 0.5rem;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home .info-line-container {
  display: flex;
  flex-direction: column;
  text-align: left;
  align-items: flex-start;
  vertical-align: bottom;
  justify-content: flex-end;
  align-items: flex-start;
  height: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.home .info-line {
  float: left;
  display: flex;
  flex-direction: row;
  background-color: #58647c;
  border-radius: 5px;
  margin: 0.2rem 2rem 0.5rem 2rem;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
}

.home .info-line-icon-holder {
  width: 50px;
  height: 50px;
  align-items: center;
  align-content: center;
  text-align: center;
  vertical-align: middle;
  justify-content: center;
  /* border: 1px solid #000; */
  background-color: #5f6b83;
}

.home .info-line-icon {
  position: relative;
  top: 10px;
}

.home .info-line-text {
  height: 50px;
  align-self: center;
  text-align: center;
  vertical-align: middle;
  justify-content: center;
  /* border: 1px solid #000; */
}

.home .info-line-text p {
  align-self: center;
  text-align: center;
  vertical-align: middle;
  position: relative;
  top: -8px;
}

.home .info-line-right {
  align-self: flex-end;
  justify-content: flex-end;
  vertical-align: end;
  margin-right: 2rem;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

.home #profile-more .info-line-text {
  margin-left: 1rem;
  margin-right: 1rem;
  /* background-color: #454d60; */
  color: #ccc;
  font-weight: bold;
  font-size: larger;
  position: relative;
}

.home .button {
  display: inline-block;
  padding: 0.5rem 1rem;
  text-align: center;
  color: white;
  font-weight: bold;
  background-color: #2a323b;
  border: 1px solid #396492;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.2s, border-color 0.2s;
}

.home .button:hover {
  background-color: #4283c8;
  border-color: #396492;
}

.home #profile-more img {
  margin: 0.7rem 2rem 0.5rem 0.7rem;
  width: 200px;
  height: 200px;
  /* box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5); */
  border-radius: 5px;
}

.home .custom-icon {
  font-size: 5rem;
  color: grey;
  transition: 0.3s;
}

.home .more-item {
  background-color: aquamarine;
  width: 50px;
  height: 50px;
  margin: 1rem 1rem 1rem 1rem;
}

.home #pic-holder {
  margin: 2rem 2rem 0.5rem 2rem;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: #3f4657;
}

.home #text-side {
  text-align: center;
  align-items: center;
  display: flex;
  flex-direction: column;
}

.home .about {
  /* background-color: #4e596f; */
  background-color: #5d6881;
  font-size: larger;
  color: #ccc;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 1rem 1rem 1rem 1rem;
  margin-left: 1rem;
  margin-right: 1rem;
  max-width: 28ch;
  font-weight: bold;
}

.home .about2 {
  /* background-color: #4e596f; */
  background-color: #525b70;
  font-size: larger;
  color: #ccc;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  padding: 1rem 1rem 1rem 1rem;
  margin-left: 2rem;
  margin-right: 2rem;
  max-width: 48ch;
  font-weight: bold;
}

.home #profile-more #first {
  font-size: large;
}

.home #profile-more #last {
  font-size: large;
}

.home #networks {
  background-color: #2d3340;
  padding: 0.7rem 0rem 0.7rem 0rem;
  border-radius: 10px;
}

.home .network {
  margin: 1rem 1rem 1rem 1rem;
}

.home .highlight {
  font-style: italic;
  color: #e2e1e1;
}

.home #projects {
  background-color: #40485a;
  min-height: 350px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  min-width: 50%;
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
}

.home #projects-more {
  background-color: #40485a;
  min-height: 350px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  min-width: 50%;
  text-align: center;
  vertical-align: middle;
  gap: 0.5rem;
  position: relative;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}

.home .vertical-block {
  width: 30%;
  height: 90%;
  display: flex;
  text-align: center;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
}

#vertical-block-left {
  width: 60%;
  height: 90%;
  display: flex;
  text-align: center;
  align-items: center;
  vertical-align: middle;
  justify-content: center;
}

#img-projects {
  /* width: 100%; */
  /* min-width: 250px; */
  opacity: 0.3;
  mask-image: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0));
  /* width: 95%; */
  /* height: 95%; */
  /* margin: 1rem 1rem 1rem 1rem; */
  background-size: 100%; /* ou background-size: 100%; */
  background-position: center;
  width: 120%;
  height: auto;
  position: absolute;
  /* bottom: 0;
    left: 50%; */
  pointer-events: none;
}

.home .more {
  display: block;
  background-color: rgba(0, 0, 0, 0.2);
  background-color: goldenrod;
  min-height: 400px;
  height: 25rem;
  width: 100%;
  margin-top: 1rem;
}

.home .welcome {
  background-color: #3a4253;
  width: 100%;
  width: 400px;
  align-items: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
}

.home .introduction {
  background-color: antiquewhite;
  width: 100%;
}

.home .profile-pic {
  background-color: #4e596f;
  margin: 1rem 1rem 1rem 1rem;
  width: 160px;
  height: 210px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
}

.home .profile-pic img:hover {
  margin: 0.5rem 0.5rem 0.5rem 0.5rem;
  width: 200px;
  height: 200px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
}

.home .whoiam {
  margin: 1rem 1rem 1rem 1rem;
  background-color: #333a49;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
}

.home .whoiam p {
  color: #ccc;
  font-weight: bold;
}

.home h2 {
  margin-bottom: 3rem;
  font-size: 3rem;
  color: #353e4c;
}

@media (max-width: 940px) {
  /* .sub-block {
      width: 50%;
    } */

  .home {
    padding: 0rem;
    padding-top: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;
    padding-bottom: 3rem;
  }

  .home .block {
    flex: 100%;
    flex-direction: column;
  }

  .home #profile {
    min-height: 450px;
    flex-direction: column;
  }

  .home #profile-more {
    min-height: 330px;
    flex-direction: column;
    align-items: center;
  }

  .home #text-side {
    flex-direction: column;
  }

  /* #profile-more .info-line {
        margin-left: 2rem;
        display: flex;
        flex-direction: row;
    }

    #profile-more .info-line-text {
        margin-left: 1rem;
        margin-right: 1rem;
        background-color: #454d60;
        color: #CCC;
        font-weight: bold;
        font-size: large;
        position: relative;
        top: -16px;
        padding: 0.2rem 1rem 0.2rem 1rem;
    } */

  .home .info-line-right {
    align-self: center;
    margin-right: 0rem;
    margin-bottom: 2rem;
  }

  .home .info-line-text {
    height: 100%;
    min-height: 50px;
  }

  .home .info-line-icon-holder {
    height: 100%;
    min-height: 50px;
    min-width: 50px;
  }

  .home .about2 {
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .home .info-line-container {
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center;
    vertical-align: bottom;
    justify-content: center;
    height: 100%;
    margin-top: 1rem;
    margin-bottom: 1rem;
  }
}
