.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .popup-inner {
    background-color: #3e3e3e;
    padding: 10px;
    border-radius: 5px;
    min-width: 370px;
    min-height: 600px;
    max-width: 90%;
    max-height: 75%;
    margin-top: 5rem;
    overflow-y: auto;
    display: block;
    border: solid 2px black;
    display: flex;
    flex-direction: column;
  }
  
  .popup-close {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: transparent;
    border: none;
    font-size: 24px;
    cursor: pointer;
    background-color: red;
    width: 10px;
    height: 10px;
  }

  .carousel-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    min-height: 300px;
    background-color: #2e2e2e;
  }

  .popup-type {
    background-color: rgb(40, 40, 40);
    color: rgb(200, 200, 200);
    /* padding: 1rem 1rem 1rem 1rem; */
    display: flex;
    flex-direction: row;
    align-items: center;
    text-align: center;
    justify-content: center;
    margin-bottom: 1rem;
    position: relative;
  }

  .right {
    background-color: #444;
    position: absolute;
    right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 5px;
  }

  .right:hover {
    background-color: #844;
    position: absolute;
    right: 10px;
    width: 40px;
    height: 40px;
    border-radius: 5px;
    cursor: pointer;
  }

  .popup h3.type {
    /* margin-top: 0rem; */
    /* margin-bottom: 1.0rem; */
    color: rgb(200, 200, 200);
    /* background-color: rgb(255, 40, 40); */
    /* padding: 1rem 1rem 1rem 1rem; */
  }

  .close {
    background-color: red;
    position: relative;
    top: 46px;
    right: 10px;
    left: 0px;
    width: 14px;
    height: 30px;
    color: red;
  }

  #close-icon {
    position: absolute;
    top: 0px;
    left: 6px;
    opacity: 0.6;
  }

  .popup p {
    max-width: 60ch;
    text-align: left;
    margin-left: 1rem;
    margin-right: 1rem;
  }

  .popup img {
    object-fit: cover;
    max-height: 300px;
    max-width: auto; 

  }

  .popup a {
    display: inline-block;
    padding: 0.5rem 1rem;
    text-align: center;
    color: white;
    font-weight: bold;
    background-color: #2a323b;
    border: 1px solid #396492;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.2s, border-color 0.2s;
  }

  .popup a:hover {
    background-color: #4283c8;
    border-color: #396492;
  }

  .popup-inner::-webkit-scrollbar {
    width: 10px;
  }
  
  .popup-inner::-webkit-scrollbar-thumb {
    background-color: #396492;
    border-radius: 5px;
  }
  
  .popup-inner::-webkit-scrollbar-track {
    background-color: #1c334c;
  }

  .links {
    background-color: rgb(40, 40, 40);
    display: flex;
    justify-content: flex-end;
    padding: 0.5rem 0.25rem 0.5rem 0.25rem;
  }
  
  .links a {
    margin: 0rem 0.25rem 0rem 0.25rem;
  }

  .description {
    background-color: #242933;
    padding: 1rem 1rem 1rem 1rem;
    position: relative;
  }