.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #323947;
  padding: 1rem;
  z-index: 1000;
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 0px 0px 2px rgba(0, 0, 0, 0.2);
  min-width: 370px;
}

.navbar-brand {
  display: flex;
  align-items: center;
}

.navbar-logo {
  height: 30px;
  margin-right: 1rem;
  padding-top: 0.30rem;
}

.navbar-title {
  font-size: 1.5rem;
  color: rgb(167, 167, 167);
  margin: 0;
}

.hamburger {
  display: none;
  background-color: transparent;
  border: none;
  color: white;
  font-size: 1.5rem;
  cursor: pointer;
  margin-right: 2rem;
}

.navbar-list {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.navbar-item {
  margin-left: 1rem;
}

.navbar-item:last-child {
  margin-right: 2rem;
}

.navbar-icon {
  padding: 0 0 0.2rem;
  margin-left: 0.5rem;
}

.navbar-item a {
  display: inline-block;
  padding: 0.5rem 1rem;
  text-align: center;
  color: white;
  font-weight: bold;
  background-color: #2a323b;
  border: 1px solid #396492;
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.2s, border-color 0.2s;
}

.navbar-item a:hover {
  background-color: #4283c8;
  border-color: #396492;
}

@media (max-width: 940px) {
  .navbar {
    display: block;
    flex-wrap: wrap;
    width: 97%;
  }

  .navbar-brand {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .navbar-title {
    font-size: 1.5rem;
    margin: 0;
  }

  .navbar-logo {
    height: 30px;
    margin-right: 1rem;
  }

  .navbar-content {
    display: flex;
    width: 100%;
  }

  .hamburger {
    display: flex;
    position: absolute;
    top: 18px;
    left: 12px;
  }

  .navbar-list {
    display: none;
  }

  .navbar-list.show {
    display: flex;
    flex-direction: column;
    background-color: #323947;
    margin: 1rem 0.5rem 1rem -1rem;
    padding: 1rem 1rem 1rem 1rem;
    align-items: center;
    justify-content: center;
    text-align: center;
  }

  .navbar-item {
    margin-left: 2rem;
    margin-right: 2rem;
    margin-top: 0;
    margin-bottom: 0.5rem;
    width: 100%;
  }

  .navbar-item a {
    display: block;
    text-align: center;
  }
}