@import './minWindowSize.css';

html, body, #root {
  color: black;
  overflow: hidden;
  height: 100%;
  width: 100%;
  margin: 0;
}

header {
  height: 40px;
}

.body::-webkit-scrollbar {
  width: 10px;
}

.body::-webkit-scrollbar-thumb {
  background-color: #396492;
  border-radius: 5px;
}

.body::-webkit-scrollbar-track {
  background-color: #1c334c;
}

.container {
  display: flex;
  flex-direction: column;
  background: black;
  height: 100%;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.content {
  background-color: #242933;
  position: relative;
  overflow-y: auto;
  margin-top: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.subcontent {
  height: 100%;
  /* min-height: 100%; */
  max-width: 1300px;
  /* display: flex; */
  text-align: center;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

@media (pointer:none), (pointer:coarse) {
  .content {
    margin-bottom: 200px;
  }

  .subcontent {
    margin-bottom: -200px;
  }
}

@media (max-width: 940px) {
  .App.menu-open .content {
    margin-top: 70px;
  }

  .App:not(.menu-open) .content {
    margin-top: 70px;
  }
}

