  .content {
    margin-top: 380px;
  }

  .subcontent {
    height: calc(100vh - 70px);
  }
} */

.content::-webkit-scrollbar {
  width: 10px;
}

.content::-webkit-scrollbar-thumb {
  background-color: #396492;
  border-radius: 5px;
}

.content::-webkit-scrollbar-track {
  background-color: #1c334c;
}


@keyframes color-sweep {
  0% {
    background-position: 200% 50%;
  }
  100% {
    background-position: -200% 50%;
  }
}

.content {
  background: linear-gradient(90deg, #242933, #222731, #242933, #222731);
  background-size: 300% 300%;
  animation: color-sweep 20s linear infinite;
}